import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import { ColumnsProps, DefaultSortProps, FilterType, getExtraData, OrderType, SearchDataGrid, SearchViewProps, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, Gps } from '../../../../models/dtos/mfp/gps';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const CodigoGpsSearchView: FC<SearchViewProps<Gps>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];

  return (
    <WindowComponent
      title="Códigos de GPS"
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'CodigoGpsEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default CodigoGpsSearchView;
